import React, { useState, useEffect } from 'react'
import axios from 'axios'

export default function MediaGallery() {

    const [items, setItems] = useState([]);
    const [selectedImage, setSelectedImage] = useState('');

    useEffect(() => {
        fetch('https://api.flickr.com/services/rest/?method=flickr.photosets.getPhotos&api_key=dd4fba161f787299af518125880e903f&photoset_id=72157719398466589&user_id=153252113@N08&extras=url_o,url_c&format=json&nojsoncallback=1')
            .then((response) => response.json())
            .then((data) => {
                console.log(data.photoset.photo);
                setItems(data.photoset.photo);
            });

        //setSelectedImage(imageURL(resp.data.photos.photo[0]));

    }, [setItems])

    return (
        <div>
            {items.length ?
                items.map((item, index) =>
                    <div key={index} className={'media-gallery-img-container'}>
                        <img src={imageURL(item)} />
                    </div>)
                : <div>Loading...</div>
            }
        </div>
    )
}

// assemble image URL
function imageURL(item: any) {

    return item.url_c


    // thumbnail
    //return 'http://farm' + item.farm + '.staticflickr.com/' + item.server + '/' + item.id + '_' + item.secret + '.jpg'
}